body {
    margin: 0;
    font-family: Arial, Tahoma;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
    margin: auto;
    width: 85%;
}

.anchor {
    display: block;
    color: #222;
    text-decoration: none;
    padding: 10px;
    font-weight: bold;
}

.anchor:hover {
    color: #eb5424
}